#above-the-fold {
  padding: 30px;
  grid-template-columns: 1fr 1fr;
}

#above-the-fold > * {
  width: 100%;
}

#intro-text {
  max-width: 575px;
}

#intro-text h1 {
  font-size: 2.5rem;
}

#intro-text h2 {
  font-size: 1.25rem;
}

#chart {
  height: 100%;
  max-width: 1000px;
}
